const initialState = {
  items: [],
  loading: false,
};

const products = (state = initialState, action) => {
  switch (action.type) {
    case 'SET_PRODUCTS_LOADING':
      return {
        ...state,
        loading: true,
      };
    case 'FETCH_PRODUCTS':
      return {
        ...state,
        items: action.payload,
        loading: false,
      };
    case 'SORT_PRODUCTS_LOW_PRICE':
      return {
        ...state,
        items: action.payload,
        loading: false,
      };
    case 'SORT_PRODUCTS_HIGH_PRICE':
      return {
        ...state,
        items: action.payload,
        loading: false,
      };
    default:
      return state;
  }
};

export default products;
